import tabBar from '../components/tabBar'
export default {
  components: { tabBar },
  data() {
    return {
      keyword: '',
      activeIndex: 0,
      tabList: [
        { name: '我接收的', url: '/list' },
        { name: '我发起的', url: '/error' }
      ],
      params: {
        pageNum: 0,
        pageSize: 20
      },
      list: []
    }
  },
  methods: {
    refresh(done) {
      this.list = []
      this.params.pageNum = 1
      this.getList(done)
    },
    infinite(done) {
      if (this.list.length > 25) {
        this.$refs.scroller.finishInfinite(true)
        if (this.list.length < 10) {
          document.getElementsByClassName('loading-layer')[0].style.display = 'none'
        }
        document.getElementsByClassName('pull-to-refresh-layer')[0].style.display = 'none'
      } else {
        this.params.pageNum++
        this.getList(done)
      }
    },
    getList(done) {
      document.getElementsByClassName('pull-to-refresh-layer')[0].style.display = 'block'
      for (let i = 0; i < 10; i++) {
        this.list.push({id: i + 1})
      }
      done && done()
    }
  }
}



